@use '../util' as *;

.cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint(medium) {
        grid-template-columns: repeat(2, 1fr);
    }
    @include breakpoint(large) {
        grid-template-columns: repeat(4, 1fr);
    }
}

.card {
    background: var(--dark-card);
    color: var(--dark-text1);
    padding: rem(25);
    border-radius: rem(5);
    text-align: center;
    flex: 1;

    &--facebook {
        border-top: rem(5) solid var(--facebook);
    }
    &--twitter {
        border-top: rem(5) solid var(--twitter);
    }
    &--instagram {
        border-top: rem(5) solid var(--instagram-start);
    }
    &--youtube {
        border-top: rem(5) solid var(--youtube);
    }

    &__platform {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: rem(28);
    }

    &__icon {
        margin-right: rem(8);
        &--facebook {

        }
        &--twitter {

        }
        &--instagram {

        }
        &--youtube {

        }
    }

    &__username {
        font-size: rem(12);
        font-weight: bold;
    }

    &__followers{
        margin-bottom: rem(25);
    }

    &__count {
        color: var(--dark-text2);
        font-weight: bold;
        letter-spacing: rem(-2);
        line-height: 1;
        margin-bottom: rem(4);
        &--big {
            font-size: rem(56);    
        }
        &--small {

        }
    }

    &__label {
        font-size: rem(12);
        letter-spacing: rem(5);
        font-weight: 400;
        color: var(--dark-text1);
        text-transform: uppercase;
    }

    &__change {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(12);
        font-weight: bold;
        &--up {
            color: var(--limegreen);
        }
        &--down {
            color: var(--brightred);
        }
        img {
            margin-right: rem(4);
        }
    }
}